"use client"
import React, { useRef, useEffect } from 'react';
import { gsap } from 'gsap';

const AnimatedSection = () => {
  // Refs for the sections
  const sectionRefs = useRef([]);
  const imageRefs = useRef([]);

  // Data for the sections
  const sections = [
    { title: 'OUR APPROACH', imageUrl: './chair-1.jpg' },
    { title: 'OUR TECHNOLOGY', imageUrl: './chair-2.jpg' },
    { title: 'OUR STORY', imageUrl: './chair-3.jpg' },
    { title: 'OUR DESIGN TEAM', imageUrl: './chair-4.jpg' }
  ];

  useEffect(() => {
    // Set initial positions for images (off-screen at the bottom)
    sectionRefs.current.forEach((section, index) => {
      const image = imageRefs.current[index];
      gsap.set(image, { y: '100%', opacity: 0 });

      // Add event listeners for mouse enter and leave
      section.addEventListener('mouseenter', () => handleMouseEnter(index));
      section.addEventListener('mouseleave', () => handleMouseLeave(index));
    });

    // Cleanup
    return () => {
      sectionRefs.current.forEach((section, index) => {
        section.removeEventListener('mouseenter', () => handleMouseEnter(index));
        section.removeEventListener('mouseleave', () => handleMouseLeave(index));
      });
    };
  }, []);

  const handleMouseEnter = (index) => {
    const image = imageRefs.current[index];
    gsap.to(image, {
      y: '0%',
      opacity: 1,
      duration: 0.6,
      ease: 'power3.out'
    });
  };

  const handleMouseLeave = (index) => {
    const image = imageRefs.current[index];
    gsap.to(image, {
      y: '-100%', // Move up and out of view
      opacity: 0,
      duration: 0.6,
      ease: 'power3.in'
    });
  };

  return (
    <div className="w-full">
      <div className="grid grid-cols-1 md:grid-cols-4 gap-0">
        {sections.map((section, index) => (
          <div
            key={index}
            ref={(el) => (sectionRefs.current[index] = el)}
            className="relative h-64 md:h-80 overflow-hidden border-r border-gray-200 last:border-r-0 bg-white"
          >
            <div className="absolute inset-0 flex items-end p-6 z-10">
              <h2 className="text-xl font-bold text-gray-800">{section.title}</h2>
            </div>
            <div
              ref={(el) => (imageRefs.current[index] = el)}
              className="absolute inset-0 w-full h-full z-0"
            >
              <img
                src={section.imageUrl}
                alt={section.title}
                className="object-cover w-full h-full"
              />
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default AnimatedSection;